oint }}\n\t\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t</template>\n\t\t\t\t\t</v-select>\n\t\t\t\t</div>\n\t\t\t\t<div class=\"col-2\">\n\t\t\t\t\t<div v-if=\"showAddButton\">\n\t\t\t\t\t\t<button @click=\"addContact\">\n\t\t\t\t\t\t\t+\n\t\t\t\t\t\t</button>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t\t<div class=\"row\">\n\t\t\t\t<div class=\"col-10\">\n\t\t\t\t\t<label for=\"txt-subject\">Subject</label>\n\t\t\t\t\t<input id=\"txt-subject\"\n\t\t\t\t\t\tv-model=\"message.subject\"\n\t\t\t\t\t\ttype=\"text\"> </input>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t\t<div class=\"row\">\n\t\t\t\t<div class=\"col-10\">\n\t\t\t\t\t<label for=\"txt-message\">Message</label>\n\t\t\t\t\t<textarea\n\t\t\t\t\t\tid=\"txt-message\"\n\t\t\t\t\t\tv-model=\"message.body\"\n\t\t\t\t\t\tplaceholder=\"type your message\"\n\t\t\t\t\t\trows=\"1\" />\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t\t<div class=\"row\">\n\t\t\t\t<div class=\"col-10\">\n\t\t\t\t\t<div class=\"items\">\n\t\t\t\t\t\t<InvoiceItemList v-model=\"message.invoiceLines\" row-number=\"1\" />\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t\t<div class=\"row\">\n\t\t\t\t<div class=\"col-10\">\n\t\t\t\t\t<label for=\"file\">Choose a XML file</label>\n\t\t\t\t\t<input id=\"file\"\n\t\t\t\t\t\ttype=\"file\"\n\t\t\t\t\t\tplaceholder=\"Choose a file or drop it here...\"\n\t\t\t\t\t\t@chane=\"setFile\" />\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t\t<div class=\"row\">\n\t\t\t\t<div class=\"col-5\">\n\t\t\t\t\t<select\n\t\t\t\t\t\tid=\"cmb-media-type\"\n\t\t\t\t\t\tv-model=\"message.mediaType\"\n\t\t\t\t\t\tsize=\"sm\"\n\t\t\t\t\t\tclass=\"mt-3\">\n\t\t\t\t\t\t<option value=\"\" disabled>\n\t\t\t\t\t\t\tSelect sending media\n\t\t\t\t\t\t</option>\n\t\t\t\t\t\t<option v-for=\"item in mediaOptions\" :key=\"item.value\" :value=\"item.value\">\n\t\t\t\t\t\t\t{{ item.text }}\n\t\t\t\t\t\t</option>\n\t\t\t\t\t</select>\n\t\t\t\t</div>\n\t\t\t\t<div class=\"col-5\">\n\t\t\t\t\t<button @click=\"submit\">