rowRemoved\">\n\t\t\t</InvoiceItem>\n\t\t</div>\n\t\t<div class=\"footer\">\n\t\t\t<div class=\"btn-plus\">\n\t\t\t\t<button type=\"button\" id=\"btn-add\" @click=\"addItem\">+</button>\n\t\t\t</div>\n\t\t\t<div class=\"gap\">\n\t\t\t</div>\n\n\t\t\t<div class=\"total-cap\">\n\t\t\t\t<p class=\"text-total\">\n\t\t\t\t\t<b>Total amount:</b>\n\t\t\t\t</p>\n\t\t\t</div>\n\t\t\t<div class=\"total-amount\">\n\t\t\t\t<p class=\"text-total\"><b> {{ sumOfItems }} </b></p>\n\t\t\t</div>\n\t\t</div>\n\n\t</div>\n</template>\n\n<script>\nimport InvoiceItem from './InvoiceItem'\nexport default {\n\tname: 'InvoiceItemList',\n\tcomponents: {\n\t\tInvoiceItem\n\t},\n\tdata() {\n\t\treturn {\n\t\t\trows: [{\n\t\t\t\ttitle: '',\n\t\t\t\tquantity: 0,\n\t\t\t\tdescription: '',\n\t\t\t\ttotalPrice: 0,\n\t\t\t\tfee: 0,\n\t\t\t}],\n\t\t\tsumOfItems: 0,\n\t\t}\n\t},\n\tmounted: function() {\n\t\tthis.$watch('rows', function() {\n\t\t\tthis.sumOfItems = this.calculateSum()\n\t\t}, { deep: true })\n\t},\n\tmethods: {\n\t\taddItem: function() {\n\t\t\tthis.rows.push({\n\t\t\t\ttitle: '',\n\t\t\t\tquantity: 0,\n\t\t\t\tdescription: '',\n\t\t\t\ttotalPrice: 0,\n\t\t\t\tfee: 0,\n\t\t\t})\n\t\t},\n\t\tcalculateSum: function() {\n\t\t\treturn this.rows.reduce((n, item) => n + item.totalPrice, 0)\n\t\t},\n\t\trowDataChanged: function(param) {\n\t\t\tthis.sumOfItems = this.calculateSum()\n\t\t\tthis.rows[param.index] = param.data\n\t\t\tthis.emitResult()\n\t\t},\n\t\trowRemoved: function(rowIndex) {\n\t\t\tif (this.rows.length > 1) {\n\t\t\t\tthis.rows.splice(rowIndex, 1)\n\t\t\t\tthis.sumOfItems = this.calculateSum()\n\t\t\t\tthis.emitResult()\n\t\t\t}\n\t\t},\n\t\temitResult: function() {\n\t\t\tconst result = {\n\t\t\t\titems: this.rows,\n\t\t\t\ttotal: this.sumOfItems,\n\t\t\t}\n\t\t\tthis.$emit('input', result)\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n\t.header{\n\t\tdisplay: flex;\n\t}\n\t.row-number-header {\n\t\ttext-align: center;\n\t}\n\t.title-header {\n\t\tflex: 0.2;\n\t\ttext-align: center;\n\t}\n\t.fee-header {\n\t\tflex: 0.1;\n\t\ttext-align: center;\n\t}\n\t.quantity-header {\n\t\tflex: 0.1;\n\t\ttext-align: center;\n\t}\n\t.total-header {\n\t\tflex: 0.1;\n\t\ttext-align: center;\n\t}\n\t.description-header {\n\t\tflex: 0.4;\n\t\ttext-align: center;\n\t}\n\t.footer{\n\t\tdisplay: flex;\n\t\tmargin-top: 1%;\n\t}\n\t.btn-plus{\n\t\tflex: 0.1;\n\t}\n\t.gap{\n\t\tflex: .4;\n\t}\n\t.total-cap{\n\t\tflex: .3;\n\t\ttext-align: center;\n\t\tbackground-color: lightgray;\n\t}\n\t.total-amount{\n\t\tflex: .3;\n\t\ttext-align: center;\n\t}\n\n\t.text-total{\n\t\tpadding-top: 4% ;\n\t}\n</style>\n"],"sourceRoot":""}]);
